



























import {Component, Vue, Watch} from 'vue-property-decorator';
import ProjectItem from '@/modules/project/components/ProjectItem.vue';
import ProjectFilter, {EProjectType, IProjectFilter} from '@/modules/project/components/ProjectFilter.vue';
import Project from '@/modules/project/models/project';
import ProjectRepository from '@/modules/project/repository/project-repository';

@Component({
  components: {
    ProjectFilter,
    ProjectItem,
  },
})
export default class HomeScreen extends Vue {
  projects: Project[] = [];
  loading: boolean = true;
  projectFilter: IProjectFilter = {
    type: EProjectType.FUNDRAISING,
    searchText: '',
    category: '',
    region: '',
    industry: '',
  };

  @Watch('projectFilter', {immediate: true, deep: true})
  onFilterChange() {
    this.fetchData();
  }

  async fetchData() {
    try {
      this.loading = true;
      this.projects = await ProjectRepository.getProjects(this.projectFilter);

      if (this.projectFilter.type === EProjectType.FUNDRAISING) {
        this.projects = this.projects.filter((project) => !project.canCreateFundRaising);
      }
    } finally {
      this.loading = false;
    }
  }


}
