



































import {Component, Prop, Vue} from 'vue-property-decorator';
import Project from '@/modules/project/models/project';
import AProgressLinearBar from '@/components/AProgressLinearBar.vue';
@Component({
  components: {
    AProgressLinearBar,
  },
})

export default class ProjectSaleProgress extends Vue {
  @Prop({required: true}) project!: Project;

  get amount() {
    return this.project.canCreateFundRaising ?
      this.project.tokenInfo.tokenAmount :
      this.project.currentFundRaising!.amount;
  }

  get balance() {
    return this.project.canCreateFundRaising ?
      this.project.balance :
      this.project.currentFundRaising!.balance;
  }

  get threshold() {
    return this.project.canCreateFundRaising ?
      this.project.tokenInfo.threshold :
      this.project.currentFundRaising!.threshold;
  }

  get price() {
    return this.project.canCreateFundRaising ?
      this.project.tokenInfo.price :
      this.project.currentFundRaising!.price;
  }

  get totalCurrency() {
    return (this.amount * this.price).toFixed(2);
  }

  get valueDeterminate() {
    if (this.balance) {
      const pBalance = this.amount - this.balance;
      const determinate = (pBalance / this.amount * 100).toFixed(2);
      return determinate;
    } else {
      return 100;
    }
  }

  get valueThreshold() {
    if (this.threshold) {
      return (this.threshold / parseFloat(this.totalCurrency) * 100).toFixed(2);
    } else {
      return 0;
    }
  }

}

