import Vue from 'vue';
import Vuex from 'vuex';
import UserStore from '@/modules/auth/store/user-store';
import AppStore from '@/modules/layout/store/app-store';


Vue.use(Vuex);

interface StoreType {
  user: UserStore;
  app: AppStore;
}

const store = new Vuex.Store<StoreType>({
  modules: {
    user: UserStore,
    app: AppStore,
  },
});

export default store;
