import Project from '@/modules/project/models/project';
import ExchangeTransactionRepository from '@/modules/exchange/repository/exchange-transaction-repository';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';
import * as firebase from 'firebase';
import WalletRepository from '@/modules/wallet/repository/wallet-repository';
import UserRepository from '@/modules/users/repository/user-repository';
import ProjectRepository from '@/modules/project/repository/project-repository';

export default class ExchangeLogic {
  public static async sellToken(amount: number, price: number, project: Project, endDate: number): Promise<boolean> {
    const profile = await UserRepository.getMyProfile();
    if (!project) {
      return false;
    }
    const transaction = new ExchangeTransaction();
    transaction.price = parseFloat(price as any);
    transaction.tokenAmount = parseFloat(amount as any);
    transaction.fromUserId = firebase.auth().currentUser!.uid;
    transaction.createdTime = new Date().getTime();
    transaction.status = 'running';
    transaction.type = 'SELL';
    transaction.endDate = endDate;
    transaction.exchangerName = profile!.firstName;
    transaction.projectId = project.id;
    transaction.tokenName = project.tokenInfo.symbol!;
    transaction.total = transaction.price * transaction.tokenAmount;
    await ExchangeTransactionRepository.createTransaction(transaction);
    await WalletRepository.withdrawToken(transaction.tokenAmount, project.id);

    return true;
  }

  public static async processPendingBuyTransaction(transaction: ExchangeTransaction): Promise<boolean> {
    const project = await ProjectRepository.getProjectById(transaction.projectId);

    if (project) {
      // Update Seller
      await ExchangeTransactionRepository.updateTransactionSeller(transaction.id, firebase.auth().currentUser!.uid);
      transaction.fromUserId = firebase.auth().currentUser!.uid;

      // Withdraw Seller's Token
      await WalletRepository.withdrawToken(transaction.tokenAmount, project.id);

      if (project.setting.verifiedTransactionRequired) {
        // Update transaction status to pending
        await ExchangeTransactionRepository.updateTransactionStatus(transaction.id, 'pending');
        return true;
      } else {
        // Process Buy Transaction
        await this.processBuyTransaction(transaction, project.id);
        return true;
      }
    }

    return false;
  }

  public static async processBuyTransaction(transaction: ExchangeTransaction, projectId: string): Promise<boolean> {
    const project = await ProjectRepository.getProjectById(projectId);

    if (project) {

      // Deposit tokens for Buyer
      await WalletRepository.depositTokenForUser(
        transaction.tokenAmount,
        transaction.projectId,
        transaction.toUserId!,
        transaction.tokenName,
      );

      // Deposit money for Seller
      await WalletRepository.depositUsdForUser(transaction.total, transaction.fromUserId!);

      // Update transaction status to success
      await ExchangeTransactionRepository.updateTransactionStatus(transaction.id, 'success');
      return true;
    }

    return false;
  }

  public static async buyToken(amount: number, price: number, project: Project, endDate: number): Promise<boolean> {
    const profile = await UserRepository.getMyProfile();
    if (!project) {
      return false;
    }
    const transaction = new ExchangeTransaction();
    transaction.price = parseFloat(price as any);
    transaction.tokenAmount = parseFloat(amount as any);
    transaction.toUserId = firebase.auth().currentUser!.uid;
    transaction.createdTime = new Date().getTime();
    transaction.status = 'running';
    transaction.type = 'BUY';
    transaction.endDate = endDate;
    transaction.exchangerName = profile!.firstName;
    transaction.projectId = project.id;
    transaction.tokenName = project.tokenInfo.symbol!;
    transaction.total = transaction.price * transaction.tokenAmount;
    await ExchangeTransactionRepository.createTransaction(transaction);
    const result = await WalletRepository.withdrawUsd(transaction.total);

    return true;
  }

  public static async processPendingSellTransaction(transaction: ExchangeTransaction): Promise<boolean> {
    const project = await ProjectRepository.getProjectById(transaction.projectId);

    if (project) {
      // Update Buyer
      await ExchangeTransactionRepository.updateTransactionBuyer(transaction.id, firebase.auth().currentUser!.uid);
      transaction.toUserId = firebase.auth().currentUser!.uid;

      // Withdraw Buyer's money
      await WalletRepository.withdrawUsd(transaction.total);

      if (project.setting.verifiedTransactionRequired) {
        // Update transaction status to pending
        await ExchangeTransactionRepository.updateTransactionStatus(transaction.id, 'pending');
      } else {
        // Process Sell Transaction
        await this.processSellTransaction(transaction, project.id);
        return true;
      }
    }
    return false;
  }

  public static async processSellTransaction(transaction: ExchangeTransaction, projectId: string): Promise<boolean> {
    const result = await WalletRepository.depositTokenForUser(
      transaction.tokenAmount,
      projectId,
      transaction.toUserId!,
      transaction.tokenName,
    );

    const result2 = await WalletRepository.depositUsdForUser(transaction.total, transaction.fromUserId!);

    await ExchangeTransactionRepository.updateTransactionStatus
    (
      transaction.id,
      'success',
    );

    return true;
  }


  public static async processRejectTransaction(transaction: ExchangeTransaction, projectId: string): Promise<boolean> {
    await WalletRepository.depositTokenForUser(
      transaction.tokenAmount,
      projectId,
      transaction.fromUserId!,
      transaction.tokenName,
    );
    await WalletRepository.depositUsdForUser(transaction.total, transaction.toUserId!);
    await ExchangeTransactionRepository.rejectTransaction(transaction.id);
    return true;
  }
}
