

























import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import Profile from '@/modules/users/models/Profile';
import UserRepository from '@/modules/users/repository/user-repository';

@Component({})
export default class ProfileScreen extends Vue {
  profile: Profile | null = null;
  loading: boolean = true;
  tabs: any = [
    {
      value: 'information',
      to: '/profile/information',
    },
    {
      value: 'kyc',
      to: '/profile/kyc',
    },
  ];
  tabSelect = 0;

  mounted() {
    this.fetchData();
  }

  @Watch('tabSelect')
  onSelectTab(index: string) {
    this.$router.push(this.tabs[index].to);
  }

  async fetchData() {
    this.loading = true;

    try {
      this.profile = await UserRepository.getMyProfile();
    } catch (e) {
    } finally {
      this.loading = false;
    }
  }
}
