import * as firebase from 'firebase';
import store from '@/setup/store';
import IUser from '@/modules/auth/models/User';

const config = {
  apiKey: 'AIzaSyDEVVqSOYFN2YbY_kSknrYFvtTvl0W6XOQ',
  authDomain: 'verichains-iam.firebaseapp.com',
  databaseURL: 'https://verichains-iam.firebaseio.com',
  projectId: 'verichains-iam',
  storageBucket: 'verichains-iam.appspot.com',
  messagingSenderId: '874403267509',
};

firebase.initializeApp(config);

firebase.auth().onAuthStateChanged((user) => {
  if (user) {
    store.commit('setUser', {
      email: user.email,
      name: user.displayName,
      id: user.uid,
      photoUrl: user.photoURL,
    } as IUser);
  } else {
    // No user is signed in.
  }
  store.commit('setLoading', false);
});

export const db = firebase.firestore();

db.settings({
  timestampsInSnapshots: true,
});


