




















































































































import {Component, Prop, Vue} from 'vue-property-decorator';
import ACategoryTypeSelect from '@/components/ACategoryTypeSelect.vue';
import ACountrySelect from '@/components/ACountrySelect.vue';
import AIndustrySelect from '@/components/AIndustrySelect.vue';
import Project from '@/modules/project/models/project';
import RULES from '@/utils/rules';
import geographic from '@/var/geographic';
import categories from '@/var/categories';

@Component({
  components: {
    ACategoryTypeSelect,
    ACountrySelect,
    AIndustrySelect,
  },
})
export default class CreateProjectStep1 extends Vue {
  @Prop() value!: Project;
  valid: boolean = true;
  file: string = '';
  url: string = '';

  get rules() {
    return {
      required: RULES.required,
      max: RULES.max(20),
    };
  }

  get geographic() {
    return geographic;
  }

  get categories() {
    return categories;
  }

  goToNextAction() {
    const isValidated = (this.$refs.form as any).validate();
    if (isValidated) {
      this.$emit('next');
    }
  }

  openUploadFile() {
    (this.$refs.filePicker as any).click();
  }

  onFileSelected() {
    const file = (this.$refs.filePicker as any).files[0];
    this.file = file.name;
    this.url = URL.createObjectURL(file);
  }
}
