






























import {Component, Prop, Watch, Vue} from 'vue-property-decorator';

@Component({})
export default class ATab extends Vue {
  @Prop() tabs!: any;
  @Prop() value!: any;
  @Prop({default: false, type: Boolean}) underline?: boolean;

  onSelectTab(index: string) {
    this.$emit('input', index);
  }

  get selectedStyle() {
    if (this.underline) {
      return 'tab-select-underline';
    }

    return 'tab-select';
  }

}
