




























































import {Component, Prop, Vue} from 'vue-property-decorator';
import ProcessTransactionStep1
  from '@/modules/exchange/components/process-transaction-steps/ProcessTransactionStep1.vue';
import ProcessTransactionStep2
  from '@/modules/exchange/components/process-transaction-steps/ProcessTransactionStep2.vue';
import ProcessTransactionStep3
  from '@/modules/exchange/components/process-transaction-steps/ProcessTransactionStep3.vue';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';
import ExchangeTransactionRepository from '@/modules/exchange/repository/exchange-transaction-repository';
import Wallet from '@/modules/wallet/models/wallet';
import WalletRepository from '@/modules/wallet/repository/wallet-repository';
import ExchangeLogic from '@/modules/exchange/exchange-logic';
import Project from '@/modules/project/models/project';
import ProjectRepository from '@/modules/project/repository/project-repository';

@Component({
  components: {
    ProcessTransactionStep1,
    ProcessTransactionStep2,
    ProcessTransactionStep3,
  },
})
export default class ProcessExchangeScreen extends Vue {
  @Prop() transactionId!: string;
  step: number = 1;
  loading: boolean = true;
  submitLoading: boolean = false;
  transaction: ExchangeTransaction = new ExchangeTransaction();
  wallet: Wallet = new Wallet();
  project: Project = new Project();

  mounted() {
    this.fetchData();
  }

  handleNextAction() {
    if (this.step === 1) {
      this.step++;
      return;
    } else if (this.step === 2) {
      this.submit();
    }
  }

  handleBackAction() {
    this.step--;
  }

  async submit() {
    this.submitLoading = true;
    try {

      if (!this.transaction) {
        return;
      }

      if (this.transaction.type === 'SELL') {
        await ExchangeLogic.processPendingSellTransaction(this.transaction);
      } else {
        await ExchangeLogic.processPendingBuyTransaction(this.transaction);
      }
      this.step++;
    } catch (e) {
    } finally {
      this.submitLoading = false;
    }
  }

  async fetchData() {
    this.loading = true;
    try {
      const transaction = await ExchangeTransactionRepository.getTransactionById(this.transactionId);
      if (transaction) {
        this.transaction = transaction;

        const project = await ProjectRepository.getProjectById(this.transaction.projectId);

        if (project) {
          this.project = project;
        }
      }
      const wallet = await WalletRepository.getMyWallet();
      if (wallet) {
        this.wallet = wallet;
      }

    } catch (e) {
    } finally {
      this.loading = false;
    }
  }
}
