import ExchangeScreen from '@/modules/exchange/views/ExchangeScreen.vue';
import ProcessExchangeScreen from '@/modules/exchange/views/ProcessExchangeScreen.vue';
import ExchangeBuyerList from '@/modules/exchange/components/exchange-tabs/ExchangeBuyerList.vue';
import ExchangeSellerList from '@/modules/exchange/components/exchange-tabs/ExchangeSellerList.vue';

const routers = [
  {
    path: '/exchange',
    name: 'exchange',
    component: ExchangeScreen,
    children: [
      {
      path: 'buyer-list',
      name: 'buyer-list',
      component: ExchangeBuyerList,
    },
      {
        path: 'seller-list',
        name: 'seller-list',
        component: ExchangeSellerList,
      },
      {
        path: '',
        component: ExchangeBuyerList,
      },
    ],
  },
  {
    path: '/exchange/:transactionId',
    name: 'process-exchange',
    component: ProcessExchangeScreen,
    props: true,
  },
];


export default routers;
