import ListMyProjectScreen from '@/modules/project/views/ListMyProjectScreen.vue';
import {ifAuthenticated} from '@/utils/router-utils';
import CreateProjectScreen from '@/modules/project/views/CreateProjectScreen.vue';
import ProjectDetailScreen from '@/modules/project/views/ProjectDetailScreen.vue';
import ProjectInvestmentScreen from '@/modules/project/views/ProjectInvestmentScreen.vue';
import InformationTab from '@/modules/project/components/project-detail-tabs/InformationTab.vue';
import TeamTab from '@/modules/project/components/project-detail-tabs/TeamTab.vue';
import FundRaisingTab from '@/modules/project/components/project-detail-tabs/FundRaisingTab.vue';
import InvestorsTab from '@/modules/project/components/project-detail-tabs/InvestorsTab.vue';
import PendingTransactionsTab from '@/modules/project/components/project-detail-tabs/PendingTransactionsTab.vue';

const router = [
  {
    path: '/my-project',
    name: 'my-project',
    component: ListMyProjectScreen,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/create-project',
    name: 'create-project',
    component: CreateProjectScreen,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/project/:projectId',
    component: ProjectDetailScreen,
    props: true,
    children: [
      {
        path: '',
        name: 'project-detail',
        component: InformationTab,
      },
      {
        path: 'team',
        name: 'my-project-team',
        component: TeamTab,
      },
      {
        path: 'fund-raising',
        name: 'my-project-fund-raising',
        component: FundRaisingTab,
      },
      {
        path: 'investors',
        name: 'my-project-investors',
        component: InvestorsTab,
      },
      {
        path: 'pending-transactions',
        name: 'my-project-pending-transactions',
        component: PendingTransactionsTab,
      },
    ],
  },
  {
    path: '/project/:projectId/invest',
    name: 'project-investment',
    component: ProjectInvestmentScreen,
    props: true,
  },
];

export default router;
