























import {Component, Vue, Watch} from 'vue-property-decorator';
import ProjectRepository from '@/modules/project/repository/project-repository';
import Project from '@/modules/project/models/project';
import ProjectFilter, {EProjectType, IProjectFilter} from '@/modules/project/components/ProjectFilter.vue';
import ProjectItem from '@/modules/project/components/ProjectItem.vue';

@Component({
  components: {
    ProjectFilter,
    ProjectItem,
  },
})
export default class ListMyProjectScreen extends Vue {
  projects: Project[] = [];
  loading: boolean = true;
  projectFilter: IProjectFilter = {
    type: EProjectType.FUNDRAISING,
  };

  @Watch('projectFilter', {deep: true, immediate: true})
  onFilterChange() {
    this.fetchData();
  }

  async fetchData() {
    try {
      this.loading = true;
      this.projects = await ProjectRepository.getMyProjects();
    } finally {
      this.loading = false;
    }
  }

}
