




























import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';
import ExchangeTransactionRepository from '@/modules/exchange/repository/exchange-transaction-repository';

@Component({})
export default class HistoryTransactionsScreen extends Vue {
  loading = true;
  headers = [
    {text: 'Created Time', align: 'center', sortable: false, value: 'time'},
    {text: 'Exchanger', align: 'center', sortable: false, value: 'seller'},
    {text: 'Asset', align: 'center', sortable: false, value: 'name'},
    {text: 'Quantity', value: 'amount', align: 'right', sortable: false},
    {text: 'Price', align: 'right', sortable: false},
    {text: 'Total', align: 'right', sortable: false},
    {text: 'Expiration Time', align: 'right', sortable: false},
    {text: 'Status', align: 'center', sortable: false},
  ];
  transactions: ExchangeTransaction[] = [];

  mounted() {
    this.fetchData();
  }

  async fetchData() {
    this.loading = true;

    try {
      this.transactions = await ExchangeTransactionRepository.getMyHistoryTransactions();
    } catch (e) {
    } finally {
      this.loading = false;
    }
  }
}
