


















import {Component, Prop, Vue} from 'vue-property-decorator';
import {isValidEmail} from '@/utils/string';
import Project, {IOwner} from '@/modules/project/models/project';
import UserRepository from '@/modules/users/repository/user-repository';
import Profile from '@/modules/users/models/Profile';

@Component({
  components: {},
})
export default class ProjectAddUser extends Vue {
  @Prop({type: Project}) project: Project;

  error: boolean = false;
  errorMessage: string = '';
  email: string = '';


  onEmailInput() {
    this.error = false;
    this.errorMessage = '';
  }

  async addUser() {
    if (this.email && this.project) {
      if (!isValidEmail(this.email)) {
        this.error = true;
        this.errorMessage = 'Invalid email address! Please enter a valid email address';
        return;
      }

      const promises: any = [];

      this.project.owners.forEach((owner: IOwner) => {
        promises.push(UserRepository.getProfileByUserId(owner.id));
      });

      let owners: any = [];

      await Promise.all(promises).then((result: any) => {
        owners = result.map((owner: Profile) => {
          return {
            id: owner.userId,
            email: owner.email,
            title: 'Manager',
          };
        });
      });

      if (owners.some((owner: any) => owner.email === this.email)) {
        this.error = true;
        this.errorMessage = 'This user is already in the list';
        return;
      }

      const user = await UserRepository.getProfileByEmail(this.email);
      if (user) {
        this.email = '';
        this.$emit('add-user', user);

      } else {
        this.error = true;
        this.errorMessage = 'User not found';
      }
    }
  }
}
