







































import {Component, Prop, Vue} from 'vue-property-decorator';
import Wallet from '@/modules/wallet/models/wallet';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';
import Project from '@/modules/project/models/project';
import {currency_format} from '@/utils/format';

@Component({
  notifications: {
    showProcessBuyTransactionError: {
      title: 'Error',
      message: 'Process Transaction Failed',
      type: 'error',
    },
    showProcessBuyTransactionSuccess: {
      title: 'Success',
      message: 'Process Transaction Successfully',
      type: 'success',
    },
  },
} as any)
export default class ProcessTransactionStep2 extends Vue {
  @Prop() value!: ExchangeTransaction;
  @Prop() wallet!: Wallet;
  @Prop() project!: Project;
  @Prop({type: Boolean, default: false}) loading: boolean;

  get currencyFormat() {
    return currency_format;
  }

  get total() {
    return this.value.tokenAmount * this.value.price;
  }

  goToNextAction() {
    this.$emit('next');
    (this as any).showProcessBuyTransactionSuccess();
  }

  goBackAction() {
    this.$emit('back');
  }
}
