














import {Component, Vue, Watch} from 'vue-property-decorator';
import geographic from '@/var/geographic';

@Component({})
export default class ARegionSelect extends Vue {
  geographic: string = '';

  get geographics() {
    return geographic;
  }

  @Watch('geographic')
  onGeographicChange() {
    this.$emit('input', this.geographic);
  }

}
