import {db} from '@/setup/firebase';
import Profile from '@/modules/users/models/Profile';
import * as firebase from 'firebase';

export default class UserRepository {
  static async createUserProfile(profile: Profile): Promise<boolean> {
    try {
      const {id, ...data} = profile.getData() as any;
      const document = await db.collection('users').add(data);
    } catch (e) {
      return false;
    }

    return true;
  }

  public static async getMyProfile(): Promise<Profile | null> {
    const user = firebase.auth().currentUser;
    if (!user) {
      return null;
    }

    const querySnapshot = await db.collection('users')
      .where('userId', '==', user.uid)
      .get();

    let profile = null;

    querySnapshot.forEach((doc) => {
      profile = new Profile(doc.data(), doc.id);
    });

    return profile;
  }

  public static async getProfileByEmail(email: string): Promise<Profile | null> {

    const querySnapshot = await db.collection('users')
      .where('email', '==', email)
      .get();

    let profile = null;

    querySnapshot.forEach((doc) => {
      profile = new Profile(doc.data(), doc.id);
    });

    return profile;
  }

  public static async getProfileByUserId(userId: string): Promise<Profile | null> {

    const querySnapshot = await db.collection('users')
      .where('userId', '==', userId)
      .get();

    let profile = null;

    querySnapshot.forEach((doc) => {
      profile = new Profile(doc.data(), doc.id);
    });

    return profile;
  }

  public static async submitKyc(): Promise<boolean> {
    const profile = await this.getMyProfile();

    await db.collection('users').doc(profile!.id).update({
      kycLevel: 1,
    });

    return true;
  }

  public static async updateProfile(firstName: string, lastName: string, position: string): Promise<boolean> {
    const profile = await this.getMyProfile();

    await db.collection('users').doc(profile!.id).update({
      firstName,
      lastName,
      position,
    });

    return true;
  }
}
