

































import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import Wallet from '../../../models/wallet';
import RULES from '@/utils/rules';

@Component({
  notifications: {
    showDepositError: {
      title: 'Error',
      message: 'Deposit Apollo Failed',
      type: 'error',
    },
    showDepositSuccess: {
      title: 'Success',
      message: 'Deposit Apollo Successfully',
      type: 'success',
    },
  },
} as any)
export default class DepositApolloWalletScreen extends Vue {
  @Prop() wallet!: Wallet;
  valid = true;

  get rules() {
    return {
      required: RULES.required,
    };
  }

  deposit() {
    const isValidated = (this.$refs.form as any).validate();
    if (isValidated) {
      this.$router.replace('/wallet/deposit');
      (this as any).showDepositSuccess();
    }
  }

  copyAddress() {
    (document.querySelector('#address') as any).select();
    document.execCommand('copy');
  }
}
