























































import {Component, Vue, Watch} from 'vue-property-decorator';
import ALogo from '@/components/ALogo.vue';
import RULES from '@/utils/rules';

@Component({
  components: {
    ALogo,
  },
  notifications: {
    showLoginError: {
      title: 'Error',
      message: 'Login Failed',
      type: 'error',
    },
    showLoginSuccess: {
      title: 'Success',
      message: 'Login Successfully',
      type: 'success',
    },
  },
} as any)
export default class LoginScreen extends Vue {
  private email: string = '';
  private password: string = '';
  private loading: boolean = false;
  private showPassword: boolean = false;
  private emailErrorMessage: string[] = [];
  private valid: boolean = true;

  get rules() {
    return {
      required: RULES.required,
      email: RULES.email,
    };
  }

  @Watch('email')
  resetEmailErrorMessage() {
    this.emailErrorMessage = [];
  }

  private async loginAction() {
    const isValidated = (this.$refs.form as any).validate();
    if (isValidated) {
      this.loading = true;
      try {
        const result = await this.$store.dispatch('signUserIn', {
          email: this.email,
          password: this.password,
        });

        if (result && result.message) {
          this.emailErrorMessage.push(result.message);
          (this as any).showLoginError();
          return;
        }
        this.$router.push({name: 'Home'});
      } finally {
        (this as any).showLoginSuccess();
        this.loading = false;
      }
    }
  }
}
