



































































import {Component, Prop, Vue} from 'vue-property-decorator';
import Project from '@/modules/project/models/project';
import ProjectRepository from '@/modules/project/repository/project-repository';
import InvestStep3 from '@/modules/project/components/invest-steps/InvestStep3.vue';
import InvestStep2 from '@/modules/project/components/invest-steps/InvestStep2.vue';
import InvestStep1 from '@/modules/project/components/invest-steps/InvestStep1.vue';
import Wallet from '@/modules/wallet/models/wallet';
import WalletRepository from '@/modules/wallet/repository/wallet-repository';
import InvestTransaction from '@/modules/project/models/invest-transaction';

@Component({
  components: {
    InvestStep1,
    InvestStep2,
    InvestStep3,
  },
})
export default class ProjectInvestmentScreen extends Vue {
  @Prop() projectId!: string;

  step: number = 1;
  loading: boolean = true;
  submitLoading: boolean = false;
  transactionInfo: InvestTransaction = new InvestTransaction();

  project: Project = new Project();
  wallet: Wallet = new Wallet();
  canBuy: boolean = false;

  mounted() {
    this.fetchData();
  }

  handleNextAction() {
    if (this.step === 1) {
      this.step++;
      return;
    } else if (this.step === 2) {
      this.submit();
    }
  }

  handleBackAction() {
    this.step--;
  }

  async submit() {
    this.submitLoading = true;
    try {
      if (!this.project) {
        return;
      }
      const transaction = await ProjectRepository.investProject(
        this.project,
        parseFloat(this.transactionInfo.tokenAmount as any),
      );

      if (transaction) {
        this.transactionInfo = transaction;
        this.step++;
      }

    } finally {
      this.submitLoading = false;
    }
  }

  async fetchData() {
    this.loading = true;
    try {
      const project = await ProjectRepository.getProjectById(this.projectId);
      if (project) {
        this.project = project;
      }

      this.transactionInfo.tokenName = this.project.tokenInfo.symbol || '';

      const wallet = await WalletRepository.getMyWallet();
      if (wallet) {
        this.wallet = wallet;
      }
    } finally {
      this.loading = false;
    }
  }
}
