import Wallet from '@/modules/wallet/models/wallet';
import {db} from '@/setup/firebase';
import * as firebase from 'firebase';
import Project from '@/modules/project/models/project';

export default class WalletRepository {
  public static async createWallet(wallet: Wallet) {
    try {
      const {id, ...data} = wallet.getData() as any;
      const document = await db.collection('wallets').add(data);
    } catch (e) {
      return false;
    }
    return true;
  }

  public static async getMyWallet(): Promise<Wallet | null> {
    const user = firebase.auth().currentUser;
    if (!user) {
      return null;
    }

    const querySnapshot = await db.collection('wallets')
      .where('userId', '==', user.uid)
      .get();

    let wallet = null;

    querySnapshot.forEach((doc) => {
      wallet = new Wallet(doc.data(), doc.id);
    });
    return wallet;
  }

  public static async getWalletForUser(userId: string): Promise<Wallet | null> {
    const querySnapshot = await db.collection('wallets')
      .where('userId', '==', userId)
      .get();

    let wallet = null;
    querySnapshot.forEach((doc) => {
      wallet = new Wallet(doc.data(), doc.id);
    });
    return wallet;
  }

  public static async depositUsd(amount: number): Promise<boolean> {
    const wallet = await this.getMyWallet();
    if (!wallet) {
      return false;
    }
    if (amount <= 0) {
      return false;
    }
    try {
      wallet.usd = wallet.usd + amount;
      wallet.history.push({
        time: new Date().toString(),
        type: 'DEPOSIT',
        amount,
        status: 'SUCCESS',
      });

      await db.collection('wallets').doc(wallet.id).update({
        usd: wallet.usd,
        history: wallet.history,
      });
    } catch (e) {
      return false;
    }
    return true;
  }

  public static async depositUsdForUser(amount: number, userId: string): Promise<boolean> {
    const wallet = await this.getWalletForUser(userId);
    if (!wallet) {
      return false;
    }
    if (amount <= 0) {
      return false;
    }
    try {
      wallet.usd = wallet.usd + amount;
      wallet.history.push({
        time: new Date().toString(),
        type: 'DEPOSIT',
        amount,
        status: 'SUCCESS',
      });

      await db.collection('wallets').doc(wallet.id).update({
        usd: wallet.usd,
        history: wallet.history,
      });
    } catch (e) {
      return false;
    }
    return true;
  }

  public static async depositTokenForUser(amount: number, projectId: string,
                                          userId: string, tokenName: string): Promise<boolean> {
    const wallet = await this.getWalletForUser(userId);
    if (!wallet) {
      return false;
    }
    if (amount <= 0) {
      return false;
    }

    const tokens = {...wallet.tokens};
    if (!tokens[projectId]) {
      tokens[projectId] = {
        balance: 0,
        name: tokenName,
      };
    }
    tokens[projectId].balance += amount;
    try {
      await db.collection('wallets').doc(wallet.id).update({
        tokens,
      });
    } catch (e) {
      return false;
    }
    return true;
  }

  public static async depositToken(amount: number, project: Project): Promise<boolean> {
    const wallet = await this.getMyWallet();
    if (!wallet) {
      return false;
    }
    if (amount <= 0) {
      return false;
    }

    const tokens = {...wallet.tokens};
    if (!tokens[project.id]) {
      tokens[project.id] = {
        balance: 0,
        name: project.tokenInfo.symbol,
      };
    }
    tokens[project.id].balance += amount;
    try {
      await db.collection('wallets').doc(wallet.id).update({
        tokens,
      });
    } catch (e) {
      return false;
    }
    return true;
  }

  public static async withdrawUsd(amount: number): Promise<boolean> {
    const wallet = await this.getMyWallet();

    if (!wallet) {
      return false;
    }

    if (wallet.usd < amount) {
      return false;
    }

    if (isNaN(amount) || amount <= 0) {
      return false;
    }

    try {
      wallet.usd = wallet.usd - amount;
      wallet.history.push({
        time: new Date().toString(),
        type: 'WITHDRAW',
        amount,
        status: 'SUCCESS',
      });
      await db.collection('wallets').doc(wallet.id).update({
        usd: wallet.usd,
        history: wallet.history,
      });
    } catch (e) {
      return false;
    }
    return true;
  }

  public static async withdrawUsdforUser(amount: number, userId: string): Promise<boolean> {
    const wallet = await this.getWalletForUser(userId);

    if (!wallet) {
      return false;
    }

    if (wallet.usd < amount) {
      return false;
    }

    if (isNaN(amount) || amount <= 0) {
      return false;
    }

    try {
      wallet.usd = wallet.usd - amount;
      wallet.history.push({
        time: new Date().toString(),
        type: 'WITHDRAW',
        amount,
        status: 'SUCCESS',
      });
      await db.collection('wallets').doc(wallet.id).update({
        usd: wallet.usd,
        history: wallet.history,
      });
    } catch (e) {
      return false;
    }
    return true;
  }

  public static async withdrawToken(amount: number, projectId: string): Promise<boolean> {
    const wallet = await this.getMyWallet();
    if (!wallet) {
      return false;
    }
    if (amount <= 0) {
      return false;
    }
    const tokens = {...wallet.tokens};
    tokens[projectId].balance -= amount;
    try {
      await db.collection('wallets').doc(wallet.id).update({
        tokens,
      });
    } catch (e) {
      return false;
    }
    return true;
  }
}
