import Vue from 'vue';
import numeral from 'numeral';
import App from './modules/layout/views/App.vue';
import i18n from '@/setup/i18n';
import store from '@/setup/store';
import '@/setup/register-components';
import '@/setup/firebase';
import '@/styles/main.styl';

/**
 * ----------Vuetify--------------
 */

import Vuetify from 'vuetify';
import 'vuetify/dist/vuetify.min.css';
import router from '@/setup/router';
/* tslint:disable */
const VueNotifications = require('vue-notifications');
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

function toast({title, message, type, timeout}: any) {
  if (type === VueNotifications.types.warn) type = 'warning'
  return (iziToast as any)[type]({title, message, timeout})
}

const options = {
  success: toast,
  error: toast,
  info: toast,
  warn: toast,
};
Vue.use(VueNotifications, options);
Vue.use(Vuetify);
/**
 * -------------------------------
 */

Vue.config.productionTip = false;


Vue.filter('dateFormat', (value: any) => {
  return new Date(value).toLocaleDateString();
});

Vue.filter('dateTimeFormat', (value: any) => {
  const date = new Date(value);
  return `${date.toLocaleDateString()} ${date.toLocaleTimeString()}`;
});

Vue.filter('currencyFormat', (value: any, decimal = 2) => {
    return numeral(+value).format(`0,0.[${'0'.repeat(decimal)}]`);
});

new Vue({
  i18n,
  store,
  router,
  render: (h) => h(App),
}).$mount('#app');
