





























import {Component, Prop, Vue} from 'vue-property-decorator';

@Component({})
export default class ADateSelect extends Vue {
  @Prop() label!: string;
  @Prop() value!: string | number;
  @Prop({type: Boolean, default: false}) readonly: boolean;
  show: boolean = false;

  get dateString() {
    if (!this.value) {
      return null;
    }
    return new Date(this.value).toISOString().substr(0, 10);
  }

  datePickEvent(value: any) {
    this.show = false;

    this.$emit('input', new Date(value).getTime());
  }
}
