// tslint:disable-next-line
const pattern: any = new RegExp(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);
const RULES = {
  required: (value: string) => !!value || 'Required',
  max: (length: number) => {
    return (value: string) => value.length <= length || `Max ${length} characters`;
  },
  minCharacter: (length: number, value: string) => {
    return () => value.length >= length || `Min ${length} characters`;
  },
  email: (value: string) => pattern.test(value) || 'Invalid e-mail.',
  isNumber: (value: number) => value > 0 || 'Must be greater than 0.',
  lessOrEqualThan: (target: number, targetName: string) => {
    return (value: number) => value <= target || `Must be less than ${targetName}.`;
  },
  greaterOrEqualThan: (target: number, targetName: string) => {
    return (value: number) => value >= target || `Must be greater than ${targetName}.`;
  },
  matchPassword: (password: string, value: string) => {
    return () => (value === password) || 'Password not match';
  },
  validDateRange: (startDate: number, endDate: number) => {
    return () => (startDate <= endDate) || 'Start Date must before End Date';
  },
};

export default RULES;
