



































import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';

@Component({})
export default class ProcessTransactionStep3 extends Vue {
  @Prop() value!: ExchangeTransaction;

  headers = [
    {text: 'Time', align: 'center', sortable: false, value: 'time'},
    {text: 'Seller', align: 'center', sortable: false, value: 'seller'},
    {text: 'Asset', align: 'center', sortable: false, value: 'name'},
    {text: 'Quantity', value: 'amount', align: 'right', sortable: false},
    {text: 'Price', align: 'right', sortable: false},
  ];

  get items() {
    return [
      {
        amount: this.value.tokenAmount,
        price: this.value.price,
        seller: this.value.exchangerName,
        name: this.value.tokenName,
        time: this.value.createdTime,
        status: this.value.status,
      },
    ];
  }
}
