




















































import {Component, Prop, Vue} from 'vue-property-decorator';
import ProjectInfoItem from '@/modules/project/components/ProjectInfoItem.vue';
import Profile from '@/modules/users/models/Profile';
import ProjectRepository from '@/modules/project/repository/project-repository';
import Project from '@/modules/project/models/project';
import UserRepository from '@/modules/users/repository/user-repository';

@Component({
  components: {
    ProjectInfoItem,
  },
})
export default class ProjectMembers extends Vue {
  @Prop({required: true}) members: Profile[];
  @Prop({type: Boolean, default: false}) withDelete: boolean;
  @Prop({type: Project, required: false, default: null}) project: Project;

  deleting: boolean = false;
  currentUser: Profile | null = null;

  async beforeMount() {
    this.currentUser = await UserRepository.getMyProfile();
  }

  canDelete(item: Profile) {
    if (this.currentUser) {
      return (this.currentUser.userId !== item.userId);
    }
    return false;
  }

  get headers() {
    const headers = [
      {text: 'Member', value: 'firstName', align: 'left', sortable: true},
      {text: 'Email', value: 'email', align: 'center'},
      {text: 'Position', value: 'position', align: 'center'},
    ];

    if (this.withDelete) {
      headers.push({text: 'Action', value: '', align: 'right', sortable: false});
    }

    return headers;
  }

  async deleteUser(user: Profile) {
    if (this.project) {
      this.deleting = true;

      try {
        const result = await ProjectRepository.removeOwner(this.project.id, user.userId);
        if (result) {
          this.$emit('reload-model');
        }
      } catch (e) {
        return;
      } finally {
        this.deleting = false;
      }

    }
  }
}
