





















import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import Wallet from '../../../models/wallet';

@Component({})
export default class WithdrawScreen extends Vue {
  @Prop() wallet!: Wallet;
  loading: boolean = true;
  tabs: any = [
    {
      value: 'paypal',
      to: '/wallet/withdraw/paypal',
    },
    {
      value: 'apollo wallet',
      to: '/wallet/withdraw/apollo-wallet',
    },
  ];
  tabSelect = 0;

  @Watch('tabSelect')
  onSelectTab(index: string) {
    this.$router.push(this.tabs[index].to);
  }
}
