

































import {Component, Prop, Vue} from 'vue-property-decorator';
import Profile from '@/modules/users/models/Profile';
import UserRepository from '@/modules/users/repository/user-repository';
import * as firebase from 'firebase';

@Component({})
export default class InformationScreen extends Vue {
  @Prop() profile!: Profile;
  loading = false;

  async submit() {
    this.loading = true;
    try {
      UserRepository.updateProfile(this.profile.firstName, this.profile.lastName, this.profile.position);
    } catch (e) {
    } finally {
      this.loading = false;
    }
  }

  get filePicker(): HTMLInputElement {
    return this.$refs.filePicker as any;
  }

  get userId() {
    return firebase.auth().currentUser!.uid;
  }

  uploadAvatar() {
    this.filePicker.click();
  }

  onFileSelected() {
    const ref = firebase.storage().ref('avatar');
    const file = (document.querySelector('#avatar') as any).files[0];
    const metadata = {
      contentType: file.type,
    };
    const task = ref.child(this.userId).put(file, metadata);
    task
      .then((snapshot: any) => snapshot.ref.getDownloadURL())
      .then((url) => {
        (this.$refs.avatar as any).refresh();
      });
  }
}
