import {Module, VuexModule, Mutation, Action} from 'vuex-module-decorators';
import IUser from '@/modules/auth/models/User';
import * as firebase from 'firebase';
import UserRepository from '@/modules/users/repository/user-repository';
import Profile from '@/modules/users/models/Profile';
import WalletRepository from '@/modules/wallet/repository/wallet-repository';
import Wallet from '@/modules/wallet/models/wallet';
import router from '@/setup/router';

interface IAuthDataModel {
  email: string;
  password: string;
}

@Module({})
export default class UserStore extends VuexModule {
  user: IUser | null = null;


  get isAuth(): boolean {
    return !!this.user;
  }

  @Mutation
  setUser(user: IUser | null) {
    this.user = user;
  }

  @Action({})
  async signUserUp({email, password, firstName, lastName}: any) {
    try {
      await firebase.auth().setPersistence(firebase.auth.Auth.Persistence.LOCAL);
      const userFirebase = await firebase.auth().createUserWithEmailAndPassword(email, password);

      const profile = new Profile();
      profile.firstName = firstName;
      profile.lastName = lastName;
      profile.userId = userFirebase.user!.uid;
      profile.email = email;
      UserRepository.createUserProfile(profile);
      const wallet = new Wallet();
      wallet.userId = userFirebase.user!.uid;
      wallet.usd = 10000;
      WalletRepository.createWallet(wallet);

      this.context.dispatch('updateUser', userFirebase);
    } catch (e) {
      return e;
    }
  }

  @Action({})
  async signUserIn({email, password}: IAuthDataModel) {
    try {
      await firebase.auth().setPersistence(firebase.auth.Auth.Persistence.LOCAL);
      const userFirebase = await firebase.auth().signInWithEmailAndPassword(email, password);

      this.context.dispatch('updateUser', userFirebase);
    } catch (e) {
      return e;
    }
  }

  @Action({})
  async logout() {
    firebase.auth().signOut();
    this.context.commit('setUser', null);
    router.push({name: 'Home'});
  }

  @Action({})
  updateUser(userFirebase: firebase.auth.UserCredential) {
    if (!userFirebase.user) {
      return;
    }

    const user: IUser = {
      email: userFirebase.user.email!,
      name: userFirebase.user.displayName,
      id: userFirebase.user.uid,
      photoUrl: userFirebase.user.photoURL,
    };

    this.context.commit('setUser', user);
  }
}
