












































































import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import ALogo from '@/components/ALogo.vue';
import RULES from '@/utils/rules';

@Component({
  components: {
    ALogo,
  },
  notifications: {
    showSignUpError: {
      title: 'Error',
      message: 'Sign Up Failed',
      type: 'error',
    },
    showSignUpSuccess: {
      title: 'Success',
      message: 'Sign Up Successfully',
      type: 'success',
    },
  },
} as any)
export default class SignUpScreen extends Vue {
  private firstName: string = '';
  private lastName: string = '';
  private email: string = '';
  private password: string = '';
  private confirmPassword: string = '';
  private showPassword: boolean = false;
  private loading: boolean = false;
  private emailErrorMessage: string[] = [];
  private valid: boolean = true;

  get rules() {
    return {
      required: RULES.required,
      isNumber: RULES.isNumber,
      email: RULES.email,
      minPassword: RULES.minCharacter(6, this.password),
      matchPassword: RULES.matchPassword(this.password, this.confirmPassword),
    };
  }

  @Watch('email')
  resetEmailErrorMessage() {
    this.emailErrorMessage = [];
  }

  private async signUpAction() {
    const isValidated = (this.$refs.form as any).validate();
    if (isValidated) {
      this.loading = true;
      try {
        const result = await this.$store.dispatch('signUserUp', {
          email: this.email,
          password: this.password,
          firstName: this.firstName,
          lastName: this.lastName,
        });

        if (result && result.message) {
          this.emailErrorMessage.push(result.message);
          (this as any).showSignUpError();
          return;
        }
        this.$router.push({name: 'Home'});
      } finally {
        (this as any).showSignUpSuccess();
        this.loading = false;
      }
    }
  }
}
