
















import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import Wallet from '@/modules/wallet/models/wallet';

@Component({})
export default class AssetsScreen extends Vue {
  @Prop() wallet!: Wallet;
  loading: boolean = true;
  tabs: any = [
    {
      value: 'fiat currencies',
      to: '/wallet/assets/fiat-currencies',
    },
    {
      value: 'investments',
      to: '/wallet/assets/asset-investments',
    },
  ];
  tabSelect = 0;

  @Watch('tabSelect')
  onSelectTab(index: string) {
    this.$router.push(this.tabs[index].to);
  }
}
