













import {Component, Vue, Watch} from 'vue-property-decorator';
import ProjectRepository from '@/modules/project/repository/project-repository';
import Profile from '@/modules/users/models/Profile';
import Project, {IOwner} from '@/modules/project/models/project';
import UserRepository from '@/modules/users/repository/user-repository';
import ProjectMembers from '@/modules/project/components/ProjectMembers.vue';
import ProjectAddUser from '@/modules/project/components/ProjectAddUser.vue';

@Component({
  components: {
    ProjectMembers,
    ProjectAddUser,
  },
})
export default class InvestorsTab extends Vue {
  members: Profile[] = [];
  project: Project | null = null;
  loading: boolean = true;

  get projectId() {
    return (this.$route.params as any).projectId;
  }

  @Watch('projectId', {immediate: true})
  async onProjectIdChange() {
    this.loading = true;
    this.project = await ProjectRepository.getProjectById(this.projectId);

    if (this.project) {
      const promises: Array<Promise<Profile | null>> = [];
      this.project.owners.forEach((owner: IOwner) => {
        promises.push(UserRepository.getProfileByUserId(owner.id));
      });

      Promise.all(promises).then((result: any) => {
        this.members = result;
        this.loading = false;
      });
    }
  }

  async addUser(user: Profile) {
    const result = await ProjectRepository.addOwner(this.projectId, user.userId, 'Manager');
    if (result) {
      await this.reloadModel();
    }
  }

  async reloadModel() {
    await this.onProjectIdChange();
  }
}
