import BaseModel from '@/modules/base/models/base-model';

export default class InvestTransaction extends BaseModel {
  id: string = '';
  userId: string = '';
  projectId: string = '';
  tokenAmount: number = 0;
  price: number = 0;
  total: number = 0;
  time: number = 0;
  tokenName: string = '';

  constructor(data?: any, id?: string) {
    super();
    if (id) {
      this.id = id;
    }

    if (data) {
      this.userId = data.userId;
      this.projectId = data.projectId;
      this.tokenAmount = data.tokenAmount;
      this.price = data.price;
      this.time = data.time;
      this.tokenName = data.tokenName;
    }
  }
}
