import {ifAuthenticated} from '@/utils/router-utils';
import ProfileScreen from '@/modules/users/views/ProfileScreen.vue';
import InformationScreen from '@/modules/users/views/InformationScreen.vue';
import VerifyKYCScreen from '@/modules/users/views/VerifyKYCScreen.vue';
import KYCScreen from '@/modules/users/views/KYCScreen.vue';

const router = [
  {
    path: '/profile',
    component: ProfileScreen,
    beforeEnter: ifAuthenticated,
    children: [
      {
        path: '',
        redirect: 'information',
      },
      {
        path: 'information',
        component: InformationScreen,
      },
      {
        path: 'kyc',
        component: KYCScreen,
      },
    ],
  },
  {
    path: '/verify-kyc',
    name: 'verify-kyc',
    component: VerifyKYCScreen,
    beforeEnter: ifAuthenticated,
  },
];

export default router;
