import store from '@/setup/store';

const withReady = (func: any) => {
  return (to: any, from: any, next: any) => {
    if (store.state.app.loading) {
      setTimeout(() => func(to, from, next), 500);
      return;
    }
    func(to, from, next);
  };
};

export const ifNotAuthenticated = withReady((to: any, from: any, next: any) => {
  if (!store.getters.isAuth) {
    next();
    return;
  }
  next('/');
});

export const ifAuthenticated = withReady((to: any, from: any, next: any) => {
  if (store.getters.isAuth) {
    next();
    return;
  }
  next('/login');
});
