
































































































































import {Component, Vue, Watch} from 'vue-property-decorator';
import Project, {IOwner} from '@/modules/project/models/project';
import ProjectRepository from '@/modules/project/repository/project-repository';
import ProjectInfoItem from '@/modules/project/components/ProjectInfoItem.vue';
import UserRepository from '@/modules/users/repository/user-repository';
import Profile from '@/modules/users/models/Profile';
import ProjectMembers from '@/modules/project/components/ProjectMembers.vue';
import ProjectSaleProgress from '@/modules/project/components/ProjectSaleProgress.vue';
import {currency_format} from '@/utils/format';

@Component({
  components: {
    ProjectInfoItem,
    ProjectMembers,
    ProjectSaleProgress,
  },
})
export default class InformationTab extends Vue {
  project: Project | null = null;
  navigations: string[] = ['INFOMATION', 'MANAGER TEAM', 'INVESTORS', 'API'];
  members: Profile[] = [];

  get projectId() {
    return (this.$route.params as any).projectId;
  }

  get fundInfos() {
    return [
      {
        icon: 'search',
        label: 'Token Price',
        text: `${currency_format(this.project!.tokenInfo.price)} ${this.project!.tokenInfo.currency}`,
      }, {
        icon: 'search',
        label: 'Start Date',
        text: '',
      }, {
        icon: 'search',
        label: 'End Date',
        text: '',
      },
    ];
  }

  get projectInformation() {
    return [
      {
        icon: 'fa-globe',
        label: 'Country',
        text: this.project!.projectInfo.country,
      },
      {
        icon: 'fa-coins',
        label: 'Strategy/Type',
        text: 'Open-Ended Fund',
      },
      {
        icon: 'fa-industry',
        label: 'Industry',
        text: this.project!.projectInfo.industry,
      },
      {
        icon: 'calendar_today',
        label: 'Vintage',
        text: 2019,
      },
      {
        icon: 'attach_money',
        label: 'Balance',
        text: `${currency_format(this.project!.balance)} ${this.project!.tokenInfo.symbol}`,
      },
    ];
  }

  get dayLeft() {
    // const dayLeft = new Date(this.project!.tokenInfo.endDate).getTime() - new Date().getTime();
    // if (dayLeft < 0) {
    //   return 0;
    // }
    //
    // return Math.floor(dayLeft / 1000 / 3600 / 24);
    return 0;
  }

  // get currencyFormat() {
  //   return currency_format;
  // }

  get raisedAmount(): number {
    if (!this.project) {
      return 0;
    }
    return (this.project.tokenInfo.tokenAmount - this.project.balance) * this.project.tokenInfo.price;
  }

  @Watch('projectId', {immediate: true})
  async onProjectIdChange() {
    this.project = await ProjectRepository.getProjectById(this.projectId);

    if (this.project) {
      const promises: Array<Promise<Profile | null>> = [];
      this.project.owners.forEach((owner: IOwner) => {
        promises.push(UserRepository.getProfileByUserId(owner.id));
      });

      Promise.all(promises).then((result: any) => {
        this.members = result;
      });
    }
  }

  goToInvestment() {
    this.$router.push({name: 'project-investment', params: {id: this.project!.id}});
  }
}
