import {db} from '@/setup/firebase';
import Project, {IFundRaising, IOwner} from '@/modules/project/models/project';
import * as firebase from 'firebase';
import WalletRepository from '@/modules/wallet/repository/wallet-repository';
import InvestTransactionRepository from '@/modules/project/repository/invest-transaction-repository';
import InvestTransaction from '@/modules/project/models/invest-transaction';
// @ts-ignore
import {IProjectFilter} from '@/modules/project/components/ProjectFilter.vue';
import Query = firebase.firestore.Query;
// tslint:disable-next-line
const fuzzySearch = require('fuzzysearch');

export default class ProjectRepository {
  public static async createProject(project: Project): Promise<boolean> {
    try {
      const {id, ...data} = project.getData() as any;
      const projectDocument = await db.collection('projects').add(data);
      if (projectDocument) {
        return true;
      }
    } catch (e) {
      return false;
    }

    return false;
  }

  public static async createFundRaising(projectId: string, fundRaising: IFundRaising): Promise<boolean> {
    fundRaising.balance = fundRaising.amount;
    const project = await this.getProjectById(projectId);
    if (project) {
      try {
        if (fundRaising.amount > project.balance) {
          return false;
        }
        if (fundRaising.threshold > (fundRaising.amount * fundRaising.price)) {
          return false;
        }

        await db.collection('projects').doc(projectId).update({
          currentFundRaising: fundRaising,
        });
        return true;
      } catch (e) {
        return false;
      }
    }
    return false;
  }

  public static async addOwner(projectId: string, id: string, position: string): Promise<boolean> {
    try {
      const project = await this.getProjectById(projectId);

      if (project) {
        project.owners.push({id, position});
        await db.collection('projects').doc(project.id).update({
          owners: project.owners,
        });
        return true;
      }

    } catch (e) {
      return false;
    }

    return false;
  }

  public static async removeOwner(projectId: string, userId: string): Promise<boolean> {
    try {
      const project = await this.getProjectById(projectId);

      if (project) {
        const index = project.owners.findIndex((owner: IOwner) => owner.id === userId);
        if (index > -1) {
          project.owners.splice(index, 1);

          await db.collection('projects').doc(project.id).update({
            owners: project.owners,
          });
          return true;
        }
      }

    } catch (e) {
      return false;
    }

    return false;
  }

  public static async getMyProjects(): Promise<Project[]> {
    const projects: Project[] = [];

    const querySnapshot = await db.collection('projects').get();

    querySnapshot.forEach((doc) => {
      const project = new Project(doc.data(), doc.id);
      if (project.owners.some((item) => {
        return item.id === firebase.auth().currentUser!.uid;
      })) {
        projects.push(project);
      }
    });

    return projects;
  }

  public static async getProjects(filter?: IProjectFilter): Promise<Project[]> {
    let projects: Project[] = [];
    let query: Query = db.collection('projects');
    if (filter && filter.category !== '') {
      query = query.where(
        'projectInfo.categoryType',
        '==',
        filter.category,
      );
    }

    const querySnapshot = await query.get();

    querySnapshot.forEach((doc) => {
      const project = new Project(doc.data(), doc.id);
      projects.push(project);
    });

    if (!filter) {
      return projects;
    }

    if (filter.searchText !== '') {
      projects = projects.filter((project) =>
        fuzzySearch(filter.searchText, project.projectInfo.name.toLowerCase()));
    }

    return projects;
  }

  public static async getFundRaisings(filter?: IProjectFilter): Promise<IFundRaising[]> {
    const projects = await this.getProjects(filter);
    const fundRaisings = projects.map((project) => project.currentFundRaising);
    return fundRaisings.filter((fundraising) => fundraising) as any;
  }

  public static async getProjectById(id: string): Promise<Project | null> {
    if (!id) {
      return null;
    }

    const querySnapshot = await db.collection('projects')
      .where(firebase.firestore.FieldPath.documentId(), '==', id)
      .get();

    let project: Project | null = null;
    querySnapshot.forEach((doc) => {
      project = new Project(doc.data(), doc.id);
    });

    return project;
  }

  public static async investProject(project: Project, tokenAmount: number) {
    if (!project.currentFundRaising) {
      return false;
    }

    let result = await WalletRepository.withdrawUsd(tokenAmount * project.currentFundRaising!.price);

    if (!result) {
      return null;
    }

    result = await WalletRepository.depositToken(tokenAmount, project);

    if (!result) {
      return null;
    }

    await db.collection('projects').doc(project.id).update({
      balance: project.balance - tokenAmount,
    });
    if (project.currentFundRaising) {
      if (tokenAmount > project.currentFundRaising.amount) {
        return false;
      }
      project.currentFundRaising.balance = project.currentFundRaising.balance - tokenAmount;
      await db.collection('projects').doc(project.id).update({
        currentFundRaising: project.currentFundRaising,
      });
    }


    const transaction = new InvestTransaction();
    transaction.price = project.tokenInfo.price;
    transaction.projectId = project.id;
    transaction.time = new Date().getTime();
    transaction.userId = firebase.auth().currentUser!.uid;
    transaction.tokenAmount = tokenAmount;
    transaction.tokenName = project.tokenInfo.symbol || '';
    transaction.total = tokenAmount * project.tokenInfo.price;

    InvestTransactionRepository.createTransaction(transaction);

    return transaction;
  }


}
