import FundScreen from '@/modules/wallet/views/FundScreen.vue';
import {ifAuthenticated} from '@/utils/router-utils';
import WalletScreen from '@/modules/wallet/views/WalletScreen.vue';
import AssetScreen from '@/modules/wallet/views/wallet/AssetsScreen.vue';
import TransactionsScreen from '@/modules/wallet/views/wallet/TransactionsScreen.vue';
import FiatCurrenciesScreen from '@/modules/wallet/views/wallet/assets/FiatCurrenciesScreen.vue';
import AssetInvestmentsScreen from '@/modules/wallet/views/wallet/assets/AssetInvestmentsScreen.vue';
import PendingTransactionsScreen from '@/modules/wallet/views/wallet/transactions/PendingTransactionsScreen.vue';
import HistoryTransactionsScreen from '@/modules/wallet/views/wallet/transactions/HistoryTransactionsScreen.vue';
import DepositScreen from '@/modules/wallet/views/wallet/deposit/DepositScreen.vue';
import DepositWithdrawHistoryScreen from '@/modules/wallet/views/wallet/fund/DepositWithdrawHistoryScreen.vue';
import WithdrawScreen from '@/modules/wallet/views/wallet/withdraw/WithdrawScreen.vue';
import DepositPaypalScreen from '@/modules/wallet/views/wallet/deposit/DepositPaypalScreen.vue';
import DepositApolloWalletScreen from '@/modules/wallet/views/wallet/deposit/DepositApolloWalletScreen.vue';
import WithdrawPaypalScreen from '@/modules/wallet/views/wallet/withdraw/WithdrawPaypalScreen.vue';
import WithdrawApolloWalletScreen from '@/modules/wallet/views/wallet/withdraw/WithdrawApolloWalletScreen.vue';
import ExchangeActionScreen from '@/modules/wallet/views/ExchangeActionScreen.vue';

const router = [
  {
    path: '/wallet',
    component: WalletScreen,
    beforeEnter: ifAuthenticated,
    children: [
      {
        path: '',
        redirect: 'assets',
      },
      {
        path: 'assets',
        component: AssetScreen,
        children: [
          {
            path: 'fiat-currencies',
            name: 'fiat-currencies',
            component: FiatCurrenciesScreen,
          },
          {
            path: 'asset-investments',
            name: 'asset-investments',
            component: AssetInvestmentsScreen,
          },
          {
            path: '',
            component: FiatCurrenciesScreen,
          },
        ],
      },
      {
        path: 'transactions',
        component: TransactionsScreen,
        children: [
          {
            path: 'pending-transactions',
            name: 'pending-transactions',
            component: PendingTransactionsScreen,
          },
          {
            path: 'transaction-history',
            name: 'transaction-history',
            component: HistoryTransactionsScreen,
          },
          {
            path: '',
            component: PendingTransactionsScreen,
          },
        ],
      },
      {
        path: 'deposit',
        component: DepositScreen,
        children: [
          {
            path: 'paypal',
            name: 'deposit-paypal',
            component: DepositPaypalScreen,
          },
          {
            path: 'apollo-wallet',
            name: 'deposit-apollo-wallet',
            component: DepositApolloWalletScreen,
          },
          {
            path: '',
            component: DepositPaypalScreen,
          },
        ],
      },
      {
        path: 'withdraw',
        component: WithdrawScreen,
        children: [
          {
            path: 'paypal',
            name: 'withdraw-paypal',
            component: WithdrawPaypalScreen,
          },
          {
            path: 'apollo-wallet',
            name: 'withdraw-apollo-wallet',
            component: WithdrawApolloWalletScreen,
          },
          {
            path: '',
            component: WithdrawPaypalScreen,
          },
        ],
      },
    ],
  },
  {
    path: '/wallet/sell/:projectId',
    name: 'exchange-action',
    component: ExchangeActionScreen,
    props: true,
  },
];

export default router;
