


































import {Component, Prop, Vue} from 'vue-property-decorator';
import Wallet from '@/modules/wallet/models/wallet';
import Project from '@/modules/project/models/project';
import InvestTransaction from '@/modules/project/models/invest-transaction';

@Component({})
export default class InvestStep3 extends Vue {
  @Prop() value!: InvestTransaction;
  @Prop() wallet!: Wallet;
  @Prop() project!: Project;

  headers = [
    {text: 'Time', align: 'center', sortable: false, value: 'time'},
    {text: 'Asset', align: 'center', sortable: false, value: 'name'},
    {text: 'Quantity', value: 'amount', align: 'right', sortable: false},
    {text: 'Price', align: 'right', sortable: false},
  ];

  get items() {
    return [
      {
        amount: this.value.tokenAmount,
        price: this.value.price,
        name: this.value.tokenName,
        time: this.value.time,
        status: 'SUCCESS',
      },
    ];
  }
}
