























































import {Component, Prop, Vue} from 'vue-property-decorator';
import Wallet from '@/modules/wallet/models/wallet';
import WalletRepository from '@/modules/wallet/repository/wallet-repository';
import Project from '@/modules/project/models/project';
import ProjectRepository from '@/modules/project/repository/project-repository';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';
import ExchangeLogic from '@/modules/exchange/exchange-logic';
import RULES from '@/utils/rules';

@Component({})
export default class ExchangeActionScreen extends Vue {
  @Prop() projectId!: string;
  loading: boolean = true;
  wallet: Wallet | null = null;
  project: Project | null = null;
  transaction: ExchangeTransaction = new ExchangeTransaction();
  exchangeLoading: boolean = false;
  valid = true;

  mounted() {
    this.fetchData();
  }

  get balance() {
    return this.wallet!.tokens[this.projectId].balance;
  }

  goBack() {
    this.$router.back();
  }

  get rules() {
    return {
      required: RULES.required,
      isNumber: RULES.isNumber,
      lessOrEqualThan: RULES.lessOrEqualThan(this.balance, 'your balance'),
    };
  }

  async submit() {
    const isValidated = (this.$refs.form as any).validate();
    if (isValidated) {
      this.exchangeLoading = true;
      try {
        if (this.transaction.type === 'SELL') {
          await ExchangeLogic.sellToken(this.transaction.tokenAmount,
            this.transaction.price, this.project!, this.transaction.endDate);
        } else {
          await ExchangeLogic.buyToken(this.transaction.tokenAmount,
            this.transaction.price, this.project!, this.transaction.endDate);
        }
        this.$router.replace('/exchange');
      } catch (e) {
      } finally {
        this.exchangeLoading = false;
      }
      this.$router.replace('/exchange');
    }

  }

  async fetchData() {
    this.loading = true;
    try {
      this.wallet = await WalletRepository.getMyWallet();
      this.project = await ProjectRepository.getProjectById(this.projectId);
    } catch (e) {
    } finally {
      this.loading = false;
    }
  }
}
