import {db} from '@/setup/firebase';
import InvestTransaction from '@/modules/project/models/invest-transaction';

export default class InvestTransactionRepository {
  public static async createTransaction(transaction: InvestTransaction): Promise<boolean> {
    try {
      const {id, ...data} = transaction.getData() as any;
      const projectDocument = await db.collection('invest-transactions').add(data);
      if (projectDocument) {
        return true;
      }
    } catch (e) {
      return false;
    }

    return false;
  }
}
