import BaseModel from '@/modules/base/models/base-model';

export interface IFundHistory {
    time: string;
    type: string;
    amount: number;
    status: string;
}

export default class Wallet extends BaseModel {
  id: string = '';
  userId: string = '';
  usd: number = 0;
  tokens: any = {};
  history: IFundHistory[] = [];

  constructor(data?: any, id?: string) {
    super();
    if (id) {
      this.id = id;
    }

    if (data) {
      this.userId = data.userId;
      this.usd = data.usd;
      this.tokens = data.tokens;
      this.history = data.history;
    }
  }
}
