import Vue from 'vue';
import VueI18n from 'vue-i18n';

Vue.use(VueI18n);

const messages = {
  en: {
    message: require('../lang/en').default,
  },
};


const i18n = new VueI18n({
  locale: 'en',
  fallbackLocale: 'en',
  messages,
});

if (module.hot) {
  module.hot.accept(['../lang/en'], () => {
    i18n.setLocaleMessage('en', require('../lang/en').default);
  });
}

export default i18n;
