
















import {Component, Prop, Watch, Vue} from 'vue-property-decorator';

@Component({})
export default class TransactionsScreen extends Vue {
  loading: boolean = true;
  tabs: any = [
    {
      value: 'pending transactions',
      to: {name: 'pending-transactions'},
    },
    {
      value: 'transaction history',
      to: {name: 'transaction-history'},
    },
  ];
  tabSelect = 0;

  @Watch('tabSelect')
  onSelectTab(index: string) {
    this.$router.push(this.tabs[index].to);
  }
}
