





















import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import Wallet from '@/modules/wallet/models/wallet';
import {currency_format} from '@/utils/format';

@Component({})
export default class AssetInvestmentsScreen extends Vue {
  @Prop() wallet!: Wallet;

  headers = [
    {text: 'Name', align: 'center', sortable: false, value: 'name'},
    {text: 'Quantity', value: 'amount', align: 'right', sortable: false},
    {text: 'Action', align: 'center', sortable: false},
  ];

  get items() {
    const items = [];

    for (const item in this.wallet.tokens) {
      items.push({
        amount: currency_format(this.wallet.tokens[item].balance),
        name: this.wallet.tokens[item].name,
        projectId: item,
      });
    }

    return items;
  }

  goToSellScreen(value: string) {
    this.$router.push({name: 'exchange-action', params: {projectId: value}});
  }
}
