import BaseModel from '@/modules/base/models/base-model';

export interface IOwner {
  id: string;
  email?: string;
  position?: string;
}

export enum EProjectStatus {
  UPCOMING = 'UPCOMING',
  ONGOING = 'ONGOING',
  ENDED = 'ENDED',
}

export interface IProjectInfo {
  name: string;
  categoryType: string;
  country?: string;
  industry?: string;
  strategy?: string;
  about?: string;
  fundsHighlight?: string;
  geographic?: string;
}

export interface ITokenInfo {
  tokenAmount: number;
  symbol?: string;
  currency?: string;
  price: number;
  threshold: number;
}

export interface IFundRaising {
  amount: number;
  balance: number;
  price: number;
  startDate: number;
  endDate: number;
  threshold: number;
}

export default class Project extends BaseModel {
  balance: number = 0;
  createdAt: number = 0;
  documents: any;
  metaInfo: { website?: string; facebook?: string; twitter?: string; telegram?: string } = {};
  owners: IOwner[] = [];
  projectInfo: IProjectInfo
    = {
    name: '',
    categoryType: '',
    country: '',
  };
  setting: { kycLevelRequired: number; verifiedTransactionRequired: boolean } = {
    kycLevelRequired: 0, verifiedTransactionRequired: false,
  };
  tokenInfo: ITokenInfo = {
    price: 0,
    threshold: 0,
    tokenAmount: 0,
  };
  currentFundRaising: IFundRaising | null = null;

  id: string = '';

  constructor(data?: any, id?: string) {
    super();
    if (id) {
      this.id = id;
    }

    if (data) {
      this.balance = data.balance;
      this.documents = data.documents;
      this.metaInfo = data.metaInfo;
      this.owners = data.owners;
      this.projectInfo = data.projectInfo;
      this.setting = data.setting;
      this.tokenInfo = data.tokenInfo;
      if (data.currentFundRaising) {
        this.currentFundRaising = data.currentFundRaising;
      }
    }

  }

  get canCreateFundRaising() {
    if (!this.currentFundRaising) {
      return true;
    }
    const now = new Date().getTime();
    if (
      this.currentFundRaising.startDate === 0 ||
      this.currentFundRaising.endDate === 0 ||
      this.currentFundRaising.endDate < now) {
      return true;
    }
    return false;

  }

  get hasRunningFundRaising() {
    if (!this.currentFundRaising) {
      return false;
    }

    const now = new Date().getTime();
    if (now >= this.currentFundRaising.startDate && now <= this.currentFundRaising.endDate) {
      return true;
    } else {
      return false;
    }
  }

  get status(): EProjectStatus {
    const now = new Date().getTime();

    // if (now < this.tokenInfo.startDate) {
    //   return EProjectStatus.UPCOMING;
    // } else if (now >= this.tokenInfo.startDate && now <= this.tokenInfo.endDate) {
    //   return EProjectStatus.ONGOING;
    // }
    return EProjectStatus.ONGOING;
  }
}
