



















































import {Component, Prop, Vue} from 'vue-property-decorator';
import Wallet from '@/modules/wallet/models/wallet';
import Project from '@/modules/project/models/project';
import RULES from '@/utils/rules';
import {currency_format} from '@/utils/format';

@Component({})
export default class InvestStep1 extends Vue {
  @Prop() wallet!: Wallet;
  @Prop() project!: Project;
  @Prop() value!: any;
  valid = true;
  minimumInvestment: number = 0;

  get currencyFormat() {
    return currency_format;
  }

  get rules() {
    const value: number = this.minimumInvestment / this.project.tokenInfo.price;
    return {
      required: RULES.required,
      greaterOrEqualThan: RULES.greaterOrEqualThan(value, 'the minimum investment'),
      enoughBalance: () => this.canBuy || 'Not enough balance',
    };
  }

  get canBuy() {
    if (!this.value.tokenAmount || !this.project.currentFundRaising) {
      return false;
    }
    const buyAmount = this.value.tokenAmount * this.project.currentFundRaising!.price;
    return this.wallet.usd >= buyAmount;
  }

  goToNextAction() {
    const isValidated = (this.$refs.form as any).validate();
    if (isValidated) {
      this.$emit('next');
    }
  }
}
