





























import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import Wallet from '../../../models/wallet';
import WalletRepository from '../../../repository/wallet-repository';
import RULES from '../../../../../utils/rules';
import {currency_format} from '@/utils/format';

@Component({
  notifications: {
    showDepositError: {
      title: 'Error',
      message: 'Deposit Failed',
      type: 'error',
    },
    showDepositSuccess: {
      title: 'Success',
      message: 'Deposit Successfully',
      type: 'success',
    },
  },
} as any)
export default class DepositPaypalScreen extends Vue {
  @Prop() wallet!: Wallet;
  amount: string = '0';
  loading: boolean = false;
  valid = true;

  get currencyFormat() {
    return currency_format;
  }

  get rules() {
    return {
      required: RULES.required,
      isNumber: RULES.isNumber,
    };
  }

  async depositAction() {
    const isValidated = (this.$refs.form as any).validate();
    if (isValidated) {
      this.loading = true;
      try {
        const result = await WalletRepository.depositUsd(parseFloat(this.amount));

        if (result) {
          this.$router.replace('/wallet');
        }
      } catch (e) {
        (this as any).showDepositError();
      } finally {
        (this as any).showDepositSuccess();
        this.loading = false;
      }
    }

  }

}
