































































































import {Component, Vue, Watch} from 'vue-property-decorator';
import ProjectRepository from '@/modules/project/repository/project-repository';
import Project, {IFundRaising} from '@/modules/project/models/project';
import ProjectMembers from '@/modules/project/components/ProjectMembers.vue';
import ProjectAddUser from '@/modules/project/components/ProjectAddUser.vue';
import RULES from '@/utils/rules';
import {currency_format} from '@/utils/format';

@Component({
  components: {
    ProjectMembers,
    ProjectAddUser,
  },
})
export default class FundRaisingTab extends Vue {
  project: Project | null = null;
  loading: boolean = true;
  fundRaising: IFundRaising = {
    endDate: 0,
    startDate: 0,
    amount: 0,
    balance: 0,
    price: 0,
    threshold: 0,
  };

  get projectId() {
    return (this.$route.params as any).projectId;
  }

  get currencyFormat() {
    return currency_format;
  }

  get rules() {
    const targetNumber = this.fundRaising.amount * this.fundRaising.price;
    return {
      required: RULES.required,
      max: RULES.max(20),
      isNumber: RULES.isNumber,
      lessOrEqualThan: RULES.lessOrEqualThan(targetNumber, 'target'),
      validDateRange: RULES.validDateRange(this.fundRaising.startDate, this.fundRaising.endDate),
    };
  }

  @Watch('projectId', {immediate: true})
  async onProjectIdChange() {
    this.loading = true;
    this.project = await ProjectRepository.getProjectById(this.projectId);
    if (this.project) {
      Object.assign(this.fundRaising, this.project.currentFundRaising);
    }
    this.loading = false;
  }

  async createFundRaising() {
    if (this.fundRaising) {
      const isValidated = (this.$refs.form as any).validate();
      if (isValidated) {
        const result = await ProjectRepository.createFundRaising(this.projectId, this.fundRaising);
        if (result) {
          await this.onProjectIdChange();
        }
      }
    }
  }

}
