




































































import {Component, Vue} from 'vue-property-decorator';
import ProjectRepository from '../../../../project/repository/project-repository';
import RULES from '@/utils/rules';

@Component({
  notifications: {
    showWithdrawError: {
      title: 'Error',
      message: 'Withdraw Apollo Failed',
      type: 'error',
    },
    showWithdrawSuccess: {
      title: 'Success',
      message: 'Withdraw Apollo Successfully',
      type: 'success',
    },
  },
} as any)
export default class WithdrawApolloWalletScreen extends Vue {
  projectTokens: any = [];
  selectedItem = 'USD';
  valid = true;

  async beforeMount() {
    const projects = await ProjectRepository.getProjects();
  }

  get items() {
    return [
      'USD',
      'ETH',
      'BTC',
      'XRP',
    ];
  }

  get rules() {
    return {
      required: RULES.required,
      isNumber: RULES.isNumber,
    };
  }

  withdraw() {
    const isValidated = (this.$refs.form as any).validate();
    if (isValidated) {
      this.$router.push({path: 'withdraw'});
      (this as any).showDepositSuccess();
    }
  }

}
