






















import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import * as firebase from 'firebase';

@Component({})
export default class AAvatar extends Vue {
  @Prop({
    required: true,
    type: String,
  }) id!: string;

  @Prop({
    type: Boolean,
    default: false,
  }) disableLoading: boolean;

  avatar: string = require('@/assets/avatar-placeholder.png');
  loading: boolean = false;

  public refresh() {
    this.onIdChanged();
  }

  @Watch('id', {immediate: true})
  async onIdChanged() {
    if (this.id) {
      this.loading = true;
      try {
        const url = await firebase.storage().ref('avatar').child(this.id).getDownloadURL();
        this.avatar = url;
      } catch (e) {
      } finally {
        this.loading = false;
      }
    }
  }
}
