


















import {Component, Vue, Watch} from 'vue-property-decorator';
import ProjectFilter, {EProjectType, IProjectFilter} from '@/modules/project/components/ProjectFilter.vue';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';
import ExchangeTransactionRepository from '@/modules/exchange/repository/exchange-transaction-repository';

@Component({
  components: {
    ProjectFilter,
  },
})
export default class ExchangeScreen extends Vue {
  filter: IProjectFilter = {
    type: EProjectType.FUNDRAISING,
    category: undefined,
    country: undefined,
    searchText: '',
  };
  loading = true;
  tabs: any = [
    {
      value: 'list of buyer',
      to: '/exchange/buyer-list',
    },
    {
      value: 'list of seller',
      to: '/exchange/seller-list',
    },
  ];
  tabSelect = 0;

  @Watch('tabSelect')
  onSelectTab(index: string) {
    this.$router.push(this.tabs[index].to);
  }
  submit(id: string) {
    this.$router.push({name: 'process-exchange', params: {transactionId: id}});
  }

}
