








































































import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import Project, {IOwner} from '@/modules/project/models/project';
import ProjectRepository from '@/modules/project/repository/project-repository';
import UserRepository from '@/modules/users/repository/user-repository';
import Profile from '@/modules/users/models/Profile';
import * as firebase from 'firebase';

@Component({
  components: {},
})
export default class ProjectDetailScreen extends Vue {
  @Prop() projectId!: string;
  project: Project | null = null;
  members: Profile[] = [];
  canProcessTransaction: boolean = false;

  get navigations() {
    return [
      {
        text: 'INFORMATION',
        route: 'project-detail',
        active: true,
        enable: true,
      },
      {
        text: 'TEAM',
        route: 'my-project-team',
        active: false,
        enable: true,
      },
      {
        text: 'FUND RAISING',
        route: 'my-project-fund-raising',
        active: false,
        enable: true,
      },
      {
        text: 'INVESTORS',
        route: 'my-project-investors',
        active: false,
        enable: false,
      },
      {
        text: 'PENDING TRANSACTIONS',
        route: 'my-project-pending-transactions',
        active: false,
        enable: this.hasPendingTransaction && this.canProcessTransaction,
      },
    ];
  }

  get hasPendingTransaction() {
    return this.project && this.project.setting.verifiedTransactionRequired;
  }

  get headers() {
    return [
      {text: 'Member', value: 'firstName'},
      {text: 'Email', value: 'email', align: 'center'},
      {text: 'Position', value: 'position', align: 'center'},
    ];
  }

  get fundInfos() {
    return [
      {
        icon: 'search',
        label: 'Token Price',
        text: `${this.project!.tokenInfo.price} USD`,
      }, {
        icon: 'search',
        label: 'Start Date',
        text: '',
      }, {
        icon: 'search',
        label: 'End Date',
        text: '',
      },
    ];
  }

  get projectInformation() {
    return [
      {
        icon: 'search',
        label: 'Country',
        text: this.project!.projectInfo.country,
      },
      {
        icon: 'search',
        label: 'Strategy/Type',
        text: 'Open-Ended Fund',
      },
      {
        icon: 'search',
        label: 'Industry',
        text: this.project!.projectInfo.industry,
      },
      {
        icon: 'search',
        label: 'Vintage',
        text: 2019,
      },
    ];
  }

  get dayLeft() {
    // const dayLeft = new Date(this.project!.tokenInfo.endDate).getTime() - new Date().getTime();
    // if (dayLeft < 0) {
    //   return 0;
    // }
    //
    // return Math.floor(dayLeft / 1000 / 3600 / 24);
    return 0;
  }

  get raisedAmount(): number {
    if (!this.project) {
      return 0;
    }
    return (this.project.tokenInfo.tokenAmount - this.project.balance) * this.project.tokenInfo.price;
  }

  get canInvest() {
    if (!firebase.auth().currentUser) {
      return false;
    }
    return this.project && !this.project.canCreateFundRaising;
  }

  @Watch('projectId', {immediate: true})
  async onProjectIdChange(id: string) {
    this.project = await ProjectRepository.getProjectById(id);

    if (this.project) {
      const promises: Array<Promise<Profile | null>> = [];
      this.project.owners.forEach((owner: IOwner) => {
        promises.push(UserRepository.getProfileByUserId(owner.id));
      });

      Promise.all(promises).then((result: any) => {
        this.members = result;
      });

      const user = await UserRepository.getMyProfile();
      if (user) {
        this.canProcessTransaction = this.project.owners.some((owner: IOwner) => owner.id === user.userId);
      }
    }
  }

  goToInvestment() {
    this.$router.push({name: 'project-investment', params: {id: this.project!.id}});
  }

  navigateTab(nav: any) {
    this.navigations.forEach((navigation: any) => {
      if (navigation.route === nav.route) {
        navigation.active = true;
      } else {
        navigation.active = false;
      }
    });
    this.$router.push({name: nav.route, params: {id: this.project!.id}});
  }
}
