















































































import {Component, Prop, Vue} from 'vue-property-decorator';
import ADateSelect from '@/components/ADateSelect.vue';
import Project from '@/modules/project/models/project';
import RULES from '@/utils/rules';

@Component({
  components: {
    ADateSelect,
  },
})
export default class CreateProjectStep2 extends Vue {
  @Prop() value!: Project;
  valid = true;

  get rules() {
    const targetNumber = this.value.tokenInfo.tokenAmount * this.value.tokenInfo.price;
    return {
      required: RULES.required,
      max: RULES.max(20),
      isNumber: RULES.isNumber,
      lessOrEqualThan: RULES.lessOrEqualThan(targetNumber, 'target'),
    };
  }

  goToNextAction() {
    const isValidated = (this.$refs.form as any).validate();
    if (isValidated) {
      this.$emit('next');
    }
  }

  goBackAction() {
    this.$emit('back');
  }

  get items() {
    return [
      'USD',
      'BTC',
      'ETH',
      'EURO',
      'KRW',
      'JPY',
    ];
  }
}
