









































































import {Component, Vue, Watch} from 'vue-property-decorator';
import CreateProjectStep1 from '@/modules/project/components/create-project-steps/CreateProjectStep1.vue';
import CreateProjectStep2 from '@/modules/project/components/create-project-steps/CreateProjectStep2.vue';
import CreateProjectStep3 from '@/modules/project/components/create-project-steps/CreateProjectStep3.vue';
import CreateProjectStep4 from '@/modules/project/components/create-project-steps/CreateProjectStep4.vue';
import ProjectRepository from '@/modules/project/repository/project-repository';
import Project from '@/modules/project/models/project';
import * as firebase from 'firebase';
import Profile from '@/modules/users/models/Profile';

@Component({
  components: {
    CreateProjectStep1,
    CreateProjectStep2,
    CreateProjectStep3,
    CreateProjectStep4,
  },
})
export default class CreateProjectScreen extends Vue {
  step: number = 1;
  loading: boolean = false;
  project: Project = new Project();

  mounted() {
    if (!this.project) {
      return;
    }
    this.project.owners.push({
      id: firebase.auth().currentUser!.uid,
      position: 'Manager',
    });
  }

  @Watch('project.tokenInfo.tokenAmount')
  onProjectChange(value: string) {
    if (!this.project) {
      return;
    }

    this.project.balance = parseFloat(value);
  }

  handleNextAction() {
    if (this.step < 4) {
      this.step++;
      return;
    }

    this.submit();
  }

  handleBackAction() {
    this.step--;
  }

  addOwner(user: Profile) {
    this.project.owners.push({id: user.userId, position: user.position});
  }

  async submit() {
    this.loading = true;
    try {
      if (!this.project) {
        return;
      }

      const result = await ProjectRepository.createProject(this.project);
      this.$router.replace({name: 'my-project'});
    } finally {
      this.loading = false;
    }
  }

}
